let static_resources_domain = '';
let startX = 0,
    startY = 0,
    startPX = 0,
    startPY = 0;
export default {
    data() {
        return {
            beginIndex: -1,
            endIndex: -1,
            imgLists: [],
            canAdd: true,
            hidden: true,
            flag: false,
            x: 0,
            y: 10,
            disabled: true,
            elements: [],
            maxRowNum: 0,
            maxColNum: 0,
            imgName: ''
        }
    },
    props: {
        sortable: {
            type: Boolean,
            default: true
        },
        imgTitle: {
            type: String,
            default: ''
        },
        maxNum: {
            type: Number,
            default: 1
        },
        button: {
            type: String,
            default: '照片'
        },
        imglist: {
            type: Array,
            default: [{
                images: []
            }]
        },
        other: {
            type: [Object, Number, String, Array, Boolean],
            default: ''
        }
    },
    watch: {
        imglist() {
            this.imgLists = this.imglist.map(item => item.img);
            if (this.imgLists.length >= this.maxNum) {
                this.canAdd = false;
            } else {
                this.canAdd = true;
            }
        }
    },
    mounted() {
        this.imgLists = this.imglist.map(item => item.img);
        if (this.imgLists.length >= this.maxNum) {
            this.canAdd = false;
        } else {
            this.canAdd = true;
        }
    },
    methods: {
        getImgUrl(url) {
            let newUrl = '';
            if (static_resources_domain) {
                newUrl = /^http|wxfile.+/.test(url) ? url : (static_resources_domain + url)
            } else {
                static_resources_domain = this.Cacher.getData('static_resources_domain');
                newUrl = /^http|wxfile.+/.test(url) ? url : (static_resources_domain + url);
            }
            return newUrl
        },
        //长按
        _longtap(e) {
            this.hidden = false;
            this.flag = true;
            this.maxRowNum = Math.ceil(this.imgLists.length / 3);
            this.maxColNum = Math.min(this.imgLists.length, 3);
            this.$emit('startmove', true)
        },
        //触摸开始
        touchstart(e) {
            this.beginIndex = e.currentTarget.dataset.index;
            this.endIndex = this.beginIndex;
            startX = this.beginIndex % 3 * 81;
            startY = Math.floor(this.beginIndex / 3) * 81 + 10;
            this.x = startX;
            this.y = startY;
            startPX = e.changedTouches[0].pageX;
            startPY = e.changedTouches[0].pageY;
        },
        //触摸结束
        touchend(e) {
            this.$emit('startmove', false);
            if (!this.flag) {
                return;
            }
            if (this.sortable && this.endIndex != this.beginIndex && this.endIndex >= 0 && this.beginIndex >= 0) {
                let data = this.imgLists;
                const endIndex = this.endIndex;
                const beginIndex = this.beginIndex;
                let tmp = data[beginIndex];
                data[beginIndex] = data[endIndex];
                data[endIndex] = tmp;
                this.imgLists = data;
                this.$emit('getImages', this.imgLists)
            }
            this.endIndex = -1;
            this.hidden = true;
            this.flag = false;
        },
        //滑动
        touchmove(e) {
            if (this.flag) {
                const x = e.touches[0].pageX;
                const y = e.touches[0].pageY;
                const absX = x - startPX;
                const absY = y - startPY;
                this.x = startX + absX;
                this.y = startY + absY;
                let rowno = Math.min(Math.floor((this.y + 40) / 80), this.maxRowNum - 1);
                let colno = Math.min(Math.floor((this.x + 40) / 80), this.maxColNum - 1);
                this.endIndex = Math.min(rowno * 3 + colno, this.imglist.length - 1);
            }
        },
        addImg() {
            var num = this.maxNum - this.imgLists.length;
            let that = this;
            if (num < 1) {
                return false;
            }
            uni.showLoading({
                title: "加载中"
            });
            uni.chooseImage({
                count: num,
                sizeType: ['compressed'],
                success: function(res) {
                    that.imgLists = that.imgLists.concat(res.tempFilePaths);
                    if (that.imgLists.length >= that.maxNum) {
                        that.canAdd = false;
                    }
                    uni.hideLoading();
                    that.$emit('getImages', that.imgLists)
                },
                fail: function() {
                    uni.hideLoading();
                }
            });
        },
        removeImg: function(index) {
            this.imgLists.splice(index, 1);
            this.imgLists = this.imgLists;
            if (this.imgLists.length < this.maxNum) {
                this.canAdd = true;
            }
            this.$emit('getImages', this.imgLists)
        },
        showImgs: function(e) {
            var currentImg = e.currentTarget.dataset.imgurl;
            let that = this;
            currentImg = /^http|wxfile.+/.test(currentImg) ? currentImg : (static_resources_domain + currentImg);
            uni.previewImage({
                urls: that.imgLists,
                current: currentImg
            })
        },
    }
}
